.nav{
    background:rgba(17, 47, 65, 0.8);
    width: 100%;
    position: fixed;
    z-index: 99;
    transition: 0.3s ease-out;
    -moz-transition: 0.3s ease-out;
    -webkit-transition: 0.3s ease-out;
    -o-transition: 0.3s ease-out;
    box-shadow: 0px 4px 8px #292929;
}
.active__link{
    color:red !important;
}
.nav__items{
    display: flex;
    padding: 0;
    margin: 0;
    float: right;
}

.nav__links{
    list-style-type: none;
    display: inline;
    padding: 20px 0px;
}

.nav__links a{
    text-decoration: none;
    color: #fff;
    padding: 20px;
}

.nav__links a:hover{
    color: #ED553B;
}
.nav__logo--wrapper{
    float: left;
    margin: 14px 0 0 14px;
    cursor: pointer;
    transition:0.5s ease-out;
    -moz-transition: 0.5s ease-out;
    -webkit-transition: 0.5s ease-out;
    -o-transition: 0.5s ease-out;
}

.nav__active{
    right: 0!important;
    transition:0.4s ease-out;
    -moz-transition: 0.4s ease-out;
    -webkit-transition: 0.4s ease-out;
    -o-transition: 0.4s ease-out;
}
.move__logo{
    left: 0!important;
    transition:0.5s ease-out;
    -moz-transition: 0.5s ease-out;
    -webkit-transition: 0.5s ease-out;
    -o-transition: 0.5s ease-out;
}
@media screen and (max-width:768px){
    nav{
        right: -100%;
    }
    .nav__logo--wrapper {
        margin: 0;
        position: absolute;
        left: -52px;
    }
    .nav__logo--wrapper img{
        padding: 10px;
        border-radius: 22px;
        background-color: rgba(22, 51, 70, 0.63);
    }
    .nav__items{
        flex-direction: column;
    }

    .nav__links{
        display: block;
    }
}