.products{
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 20px 0;
    transition: 0.5s;
    -moz-transition: 0.5s;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
}
.hidde{
    display: none;
}

.products__wrapper{
    position: relative;
    width: 182px;
    height: 272px;
    margin: 10px 40px;
}

.product__single--wrapper{
    width: 180px;
    height: 300px;
}
.product__single{
    position: relative;
    text-align:center;
    width: 150px;
    height: 240px;
    border: 1px solid rgba(17, 47, 65, 0.2);
    border-radius: 5px;
    padding: 15px;
    box-shadow:0px 14px 25px -10px rgb(107, 107, 107);
    cursor: pointer;
    background: #fff;
    transition: 0.5s;
    -moz-transition: 0.5s;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
}
.product__full--desc{
    position: absolute;
    text-align: center;
    padding: 15px;
    top: 0px;
    height: 240px;
    width: 150px;
    transition: 0.5s;
    -moz-transition: 0.5s;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
}
@media screen and (max-width:930px){
    .product__full--desc{
        top: 0px;
        background: rgba(17, 47, 65, 1);
        color:#fff;
    }
}
@media screen and (min-width:930px){
    .product__full--desc{
        animation: showContent 1s ease-in forwards;
        -webkit-animation: showContent 1s ease-in  forwards;
    }
    @keyframes showContent{
        0%{
            top:0;
        }
        60%{
            top: 300px;
        }
        
        70%{
            top: 290px;
        }
        100%{
            top: 300px;
        }
    }
    
}

.product__active--link{
    position: absolute;
    left: 5%;
    z-index: 3;
    width: 90%!important;
    height: auto!important;
    transition: 2s;
    -moz-transition: 2s;
    -webkit-transition: 2s;
    -o-transition: 2s;
}
.product__single:hover{
    box-shadow: 0px 19px 25px -10px rgb(107, 107, 107);
}
.product__image{
    width: 80px;
}
.product__name{
    font-size: 14px;
}
.product__button{
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    width: 45%;
    max-width: 200px;
}
