.footer__subscribe{
    background: url('../images/meeting.jpg') no-repeat;
    background-size: cover;
    width: 100%;
    position: relative;
}
li{
    list-style-type: none;
}
a{
    text-decoration: none;
    color:#717171;
}
a:hover{
    text-decoration: underline;
    color: #444444;
}
.footer__subscribe:after{
    content: '';
    display: block;
    clear: both;
    position: relative;
    width: 80%;
    border-bottom:7px solid #F2B134;
    margin: 0 auto;
    bottom: -7px;
}
.footer__form{
    position: relative;
    text-align: center;
    padding: 80px 0;
    z-index: 11;
}
button,
[type="button"]{
    border: none;
}
button:active{
    border: none;
    outline: none;
}
.footer__form input{
    padding: 10px;
    border-radius: 5px;
    background: rgba(250,250,250,0.1);
    border: 1px solid rgba(250,250,250,0.3);
    margin: 15px;
    color: #fff;
}
.footer__title{
    color: #fff;
    font-size: 28px;
}
.footer__title span{
    font-weight: 900;
}
.footer__links{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 85%;
    margin: 0 auto;
}
.footer__col--title{

    font-weight: 600;
    text-align: center;
}
.footer__store--icon{
    width: 120px;
}
.footer__payment--icon{
    margin: 8px;   
}
.footer__section{
    position: relative;
    transition: 0.5s;
    -moz-transition: 0.5s;
    -webkit-transition: 0.5s;
    z-index: 15;
}
.line1, .line2{
    display: none;
}
@media screen and (max-width:768px){
    .footer__links{
        display: flex;
        flex-direction: column;
        
        justify-content: start;
        width: 95%;
        
    }  
    .line1, .line2{
        display: block;
        position: absolute;
        width: 15px;
        height: 3px;
        background: #000;
        right: 0;
        top: 20px;
        transition: 0.5s;
        -moz-transition: 0.5s;
        -webkit-transition: 0.5s;
        z-index: 14;
        cursor: pointer;
    }
    .line2{
        transform: rotateZ(90deg);
        -moz-transform: rotateZ(90deg);
        -webkit-transform: rotateZ(90deg);
    }

    .footer__col--title{
        position: relative;
        text-align: left;
        z-index: 15;
        cursor: pointer;
        margin-bottom: 40px;
    }
    .footer__section{
        text-align: right;
        height: 55px;
        overflow: hidden;
        transition: 0.5s;
        -moz-transition: 0.5s;
        -webkit-transition: 0.5s;
    }
    .footer__active{
        height: 250px;
        transition: 0.5s;
        -moz-transition: 0.5s;
        -webkit-transition: 0.5s;
    }
    
}