.news__wrapper{
    position: relative;
    margin: 80px 0;
}
.news__headline{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}
.news__content{
    text-align: center;

}
.news__images--wrapper{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;
}
.news__images--wrapper:after{
    content: '';
    display: block;
    clear: both;
    position: absolute;
    width: 85%;
    height: 8px;
    background:red;
    top: 205px;
    z-index: 32;
}   
.news__cam--wrapper{
    z-index: 33;
    text-align: center;
}
.news__cam--wrapper:nth-child(1){
    margin-top: 46px;
}
.news__cam--wrapper:nth-child(2){
    margin-top: 135px ;
}
.news__cam--wrapper:nth-child(3){
    margin-top: 205px ;
}
.news__button{
    height: 22px;
    margin: 34px;
}
.news__cam{
    width: 250px;
    margin-left: 40px;
    margin-right: 40px;
    display: block;
}

@media screen and (max-width:768px){
    .news__images--wrapper:after{
      display: none;
    } 
}
@media screen and (max-width:540px){
    .news__cam{
        width: 90%;
        margin: 0 auto;
      
    }
}