header{
    background:url('../../Assets/images/house.jpeg');
    background-attachment: fixed;
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 500px;
    position: relative;
  }
  .header__filter{
    width: 100%;
    height: 100%;
    background:-linear-gradient(rgba(17, 47, 65, 0.5),rgba(17, 47, 65, 1));
    background:-webkit-linear-gradient( top left ,rgba(17, 47, 65, 0.8),rgba(17, 47, 65, 1));
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.header__content--left{
    margin: 250px 0 0 120px;
    color: #fff;
}
  .header__title{
    bottom: 0;
    font-size: 44px;
    color: #fff;
    margin: 0;
}
.header__title span{
    font-weight: 900;
    color: #ED553B;
}
.learn__more--button{
    border-radius: 3px;
    background: #F2B134;
    padding: 13px 30px;
    font-size: 14px;
    text-decoration: none;
    box-shadow: 2px 3px 5px rgba(17, 47, 65, 0.7);
    color: #fff; 
    cursor: pointer;
}
.learn__more--button:hover{
    text-decoration: none;
    background: rgb(212, 142, 11);
}
.header__box--one{
    width: 50%;
}

@media screen and (max-width:768px){
    header{
        height: 100%;
    }
    .header__content--left{
        margin:120px 10%;
    }
    .header__box--one{
        width: 100%;
    }
}
.header__box--two{
    position: relative;
    height: 100%;
    width: 50%;
}
.header__box--two:after{
    content: '';
    display: block;
    clear: both;
}
.header__responsive--designe{
    width: 300px;
    position: absolute;
    bottom: -25px;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
}
@media screen and (max-width:768px){
    .header__box--two{
        height: 150px;
        width: 100%;
    }
}