.world__wrapper{
    position: relative;
    text-align: center;
    background: url('../images/world.jpeg') no-repeat;
    background-size: cover;
    background-position: center;
}
.world__content{
    z-index: 12;
    position: relative;
    padding: 100px 0 120px 0 ;

}
@media screen and (max-width:768px){
    .world__content{
        padding: 100px 10px 120px ;
    }
        
}