.award__wrapper{
    background: url('../images/meeting.jpg') no-repeat;
    background-size: cover;
    position: relative;
}
.award__wrapper:after{
    content: '';
    display: block;
    clear: both;
    width: 85%;
    position: absolute;
    height: 8px;
    background: #ED553B;
    bottom: -8px;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
}
.award__container{
    position: relative;
    z-index: 11;
    text-align: center;
    padding: 100px 0;
}
.award__icon--image{
    width: 90px;
    margin: 0 30px;
}

