.weather__boats--wrapper{
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 8px 0 0 0;
}

.weather__wrapper, .boats__wrapper{
    position: relative;
    width: 50%;
    height: 500px;
}
.weather__wrapper{
    background: url('../images/weather.jpeg') no-repeat;
    background-size: cover;
    margin-top: -60px;
    z-index: 90;
}
.weather__filter{
    position: absolute;
    width: 100%;
    height: 100%;
    background: -webkit-linear-gradient( to bottom ,#fff,#fff,rgba(17, 47, 65, 0.5));
    background: -o-linear-gradient( to bottom ,#fff,#fff,rgba(17, 47, 65, 0.5));
    background: linear-gradient( to bottom ,#fff,#fff,rgba(17, 47, 65, 0.5));
    z-index: 9; 
}
.weather__content{
    position: relative;
    z-index: 10;
}

.weather__text{
    padding: 40px 10%;
}

.boats__wrapper{
    background: url('../images/boat.jpeg') no-repeat;
    background-size: cover;
}

.boat__content{
    position: relative;
    z-index: 34;
    padding: 20px 50px;
}
.boat__text{
    color: #fff;
}
@media screen and (max-width:768px){
    .weather__wrapper, .boats__wrapper{
        width: 100%;
    }
    .boat__content{
        padding: 10px 20px;
    }
        
}