.cloud__wrapper{
    margin: 80px 0;
}
.cloud__set--wrapper{
    position: relative;
}

.cloud__set--line{
    position: absolute;
    width: 80%;
    height: 8px;
    background: #F2B134;
    top: 100px;
    z-index: 19;
    left: 50%;
    transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
}
.cloud__image--wrapper{
    z-index: 20;
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
}
.cloud__image{
    width: 235px;
    text-align: center;
}
.cloud__image:nth-child(1){
    margin: 20px 0 0 0;
}
.cloud__image:nth-child(2){
    margin: 40px 0 0 0;
}
.cloud__set--image{
    width: 170px;
}

.cloud__set--image:after{
    content: '';
    display: block;
    clear: both;
}
@media screen and (max-width:768px){

    .cloud__set--line{
        display: none;
    }
}