.backyart__wrapper{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.backyard__block--right, .backyard__block--left{
    width: 50%;
}
.hidden{
    animation: hidde 1s forwards;
}
@keyframes hidde{
    from{opacity: 1;}
    to{opacity: 0;}
}
.backyard__block--right{
    position: relative;
    background: url('../images/bcy-right.jpeg') no-repeat;
    background-size: cover;
    height: 450px;
    top: 60px;
    z-index: 11;
}
.backyard__sinlgle--camera{
    width: 100px;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
}
.backyard__why{
    position: relative;
    color: #fff;
    z-index: 10;
    padding: 80px 50px;
    text-align: center;
}

.backyard__live{
    padding: 60px 40px;
}

.backyard__block--left{
    position: relative;
    text-align: center;
}
.backyard__single--camera{
    width: 100px;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
}
.backyard__products--all{
    background: url('../images/backyard.jpeg') no-repeat;
    background-size: cover;
    min-height: 500px;
    background-position: bottom;
    position: relative;
}

@media screen and (max-width:768px){
    .backyard__block--right, .backyard__block--left{
        width: 100%;
    }
    .backyard__block--left{
        height: 500px;
        padding: 0 20px;
    }
    .backyard__block--right{
        top: 0;
    }
}