.filter{
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(17, 47, 65, 0.8);
    z-index: 9;
}
.white__filter{
    position: absolute;
    width: 100%;
    height: 100%;
    background: -webkit-linear-gradient( to bottom ,#fff,rgba(17, 47, 65, 0.5));
    background: linear-gradient( to bottom ,#fff,rgba(17, 47, 65, 0.5));
    z-index: 7;
}
.title{
    text-align: center;
    font-size: 32px;
    color: #000;
}
.white__title{
    text-align: center;
    font-size: 32px;
    color: #fff;
}
.title__sp--white,
.title__sp--yellow,
.title__sp--green,
.title__sp--blue,
.title__sp--red{
    font-weight: 900;
}
.title__sp--white{
    color: #fff;
}
.title__sp--blue{
    color: rgb(6, 85, 138);
}
.title__sp--yellow{
    color: #F2B134;
}
.title__sp--green{
    color:rgb(15, 177, 75);
}
.title__sp--red{
    color: #ED553B;
}
.sub__components{
    padding: 50px;
    text-align: center;
    text-transform: capitalize;
}