body{
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    position: relative;
}

.nav{
    background:rgba(17, 47, 65, 0.8);
    width: 100%;
    position: fixed;
    z-index: 99;
    transition: 0.3s ease-out;
    -moz-transition: 0.3s ease-out;
    -webkit-transition: 0.3s ease-out;
    -o-transition: 0.3s ease-out;
    box-shadow: 0px 4px 8px #292929;
}
.active__link{
    color:red !important;
}
.nav__items{
    display: flex;
    padding: 0;
    margin: 0;
    float: right;
}

.nav__links{
    list-style-type: none;
    display: inline;
    padding: 20px 0px;
}

.nav__links a{
    text-decoration: none;
    color: #fff;
    padding: 20px;
}

.nav__links a:hover{
    color: #ED553B;
}
.nav__logo--wrapper{
    float: left;
    margin: 14px 0 0 14px;
    cursor: pointer;
    transition:0.5s ease-out;
    -moz-transition: 0.5s ease-out;
    -webkit-transition: 0.5s ease-out;
    -o-transition: 0.5s ease-out;
}

.nav__active{
    right: 0!important;
    transition:0.4s ease-out;
    -moz-transition: 0.4s ease-out;
    -webkit-transition: 0.4s ease-out;
    -o-transition: 0.4s ease-out;
}
.move__logo{
    left: 0!important;
    transition:0.5s ease-out;
    -moz-transition: 0.5s ease-out;
    -webkit-transition: 0.5s ease-out;
    -o-transition: 0.5s ease-out;
}
@media screen and (max-width:768px){
    nav{
        right: -100%;
    }
    .nav__logo--wrapper {
        margin: 0;
        position: absolute;
        left: -52px;
    }
    .nav__logo--wrapper img{
        padding: 10px;
        border-radius: 22px;
        background-color: rgba(22, 51, 70, 0.63);
    }
    .nav__items{
        flex-direction: column;
    }

    .nav__links{
        display: block;
    }
}
.products{
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 20px 0;
    transition: 0.5s;
    -moz-transition: 0.5s;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
}
.hidde{
    display: none;
}

.products__wrapper{
    position: relative;
    width: 182px;
    height: 272px;
    margin: 10px 40px;
}

.product__single--wrapper{
    width: 180px;
    height: 300px;
}
.product__single{
    position: relative;
    text-align:center;
    width: 150px;
    height: 240px;
    border: 1px solid rgba(17, 47, 65, 0.2);
    border-radius: 5px;
    padding: 15px;
    box-shadow:0px 14px 25px -10px rgb(107, 107, 107);
    cursor: pointer;
    background: #fff;
    transition: 0.5s;
    -moz-transition: 0.5s;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
}
.product__full--desc{
    position: absolute;
    text-align: center;
    padding: 15px;
    top: 0px;
    height: 240px;
    width: 150px;
    transition: 0.5s;
    -moz-transition: 0.5s;
    -webkit-transition: 0.5s;
    -o-transition: 0.5s;
}
@media screen and (max-width:930px){
    .product__full--desc{
        top: 0px;
        background: rgba(17, 47, 65, 1);
        color:#fff;
    }
}
@media screen and (min-width:930px){
    .product__full--desc{
        animation: showContent 1s ease-in forwards;
        -webkit-animation: showContent 1s ease-in  forwards;
    }
    @-webkit-keyframes showContent{
        0%{
            top:0;
        }
        60%{
            top: 300px;
        }
        
        70%{
            top: 290px;
        }
        100%{
            top: 300px;
        }
    }
    @keyframes showContent{
        0%{
            top:0;
        }
        60%{
            top: 300px;
        }
        
        70%{
            top: 290px;
        }
        100%{
            top: 300px;
        }
    }
    
}

.product__active--link{
    position: absolute;
    left: 5%;
    z-index: 3;
    width: 90%!important;
    height: auto!important;
    transition: 2s;
    -moz-transition: 2s;
    -webkit-transition: 2s;
    -o-transition: 2s;
}
.product__single:hover{
    box-shadow: 0px 19px 25px -10px rgb(107, 107, 107);
}
.product__image{
    width: 80px;
}
.product__name{
    font-size: 14px;
}
.product__button{
    position: absolute;
    bottom: 20px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 45%;
    max-width: 200px;
}

.product__info--wrapper{
    width: 90%;
    margin: 0px auto;
    padding: 165px 0 35px 0;
    border-top: 1px solid #b3b3b3;
}
table{
    border-collapse: collapse;
    margin: 50px auto;
}
td, th {
    border:1px solid rgb(209, 209, 209);
    border-radius: 4px;
    padding: 10px 15px;
    text-align: left;
}
tr:nth-child(even) {
    background-color:rgba(17, 47, 65, 0.4);
    
}


.product__description{
    width: 80%;
    margin: 0 auto;
}
.product__description--text{
    line-height: 1.4em;
    text-align: center;
}
.product__description--image{
    width: 100px;
}
@media screen and (max-width:930px){
    .product__info--wrapper {
        padding: 35px 0 35px 0;
    }
}
@media screen and (max-width:768px){
    .product__description{
        width: 90%;
    }   
}
.filter{
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(17, 47, 65, 0.8);
    z-index: 9;
}
.white__filter{
    position: absolute;
    width: 100%;
    height: 100%;
    background: -webkit-linear-gradient( to bottom ,#fff,rgba(17, 47, 65, 0.5));
    background: -webkit-linear-gradient( top ,#fff,rgba(17, 47, 65, 0.5));
    background: linear-gradient( to bottom ,#fff,rgba(17, 47, 65, 0.5));
    z-index: 7;
}
.title{
