    background-position: center;
    background-size: cover;
    width: 100%;
    height: 500px;
    position: relative;
  }
  .header__filter{
    width: 100%;
    height: 100%;
    background:-linear-gradient(rgba(17, 47, 65, 0.5),rgba(17, 47, 65, 1));
    background:-webkit-linear-gradient( top left ,rgba(17, 47, 65, 0.8),rgba(17, 47, 65, 1));
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.header__content--left{
    margin: 250px 0 0 120px;
    color: #fff;
}
  .header__title{
    bottom: 0;
    font-size: 44px;
    color: #fff;
    margin: 0;
}
.header__title span{
    font-weight: 900;
    color: #ED553B;
}
.learn__more--button{
    border-radius: 3px;
    background: #F2B134;
    padding: 13px 30px;
    font-size: 14px;
